},function (){var _vm=this,_c=_vm._self._c;return _c('div',{staticClass:"demo"},[_c('div',{staticClass:"head2"},[_vm._v("BlueBee Demo")]),_c('div',{staticClass:"show"},[_c('div',{staticClass:"vedios"},[_c('iframe',{attrs:{"width":"562","height":"316","src":"https://www.youtube.com/embed/ZAtHBLRmEUs","frameborder":"0","allow":"accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture","allowfullscreen":""}})]),_c('div',{staticClass:"description"},[_vm._v(" The cross-technology communication (CTC) is a promising technique proposed recently to bridge heterogeneous wireless technologies in the ISM bands. Existing solutions use only the coarse-grained packet-level information for CTC modulation, suffering from a low throughput (e.g., 10 b/s). BlueBee explores the PHY-layer information for CTC by emulating legitimate ZigBee frames with the Bluetooth radio, achieving dual-standard compliance and transparency. Our implementation on both USRP and commodity devices shows that BlueBee can achieve standard ZigBee bit rate of 250 kb/s at more than 99% accuracy, 10000x faster than the start-of-the-art packet-level CTC technologies. ")])]),_c('div',{staticClass:"head2"},[_vm._v("LTE2B Demo")]),_c('div',{staticClass:"show"},[_c('div',{staticClass:"vedios"},[_c('iframe',{attrs:{"width":"562","height":"316","src":"https://www.youtube.com/embed/ycOY9y__3bw","frameborder":"0","allow":"accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture","allowfullscreen":""}})]),_c('div',{staticClass:"description"},[_vm._v(" LTE2B enables WAN/WLAN devices, e.g., LTE smartphones and WiFi Access Points, to directly transmit data to low-power IoT devices such as ZigBee and Bluetooth. This is achieved via Time-domain Emulation that enables WAN/WLAN transmitters to accurately emulate the ZigBee/Bluetooth waveform. We implement LTE2B on commodity devices (Nexus 5X smartphone and CC2530/CC1350 ZigBee/BLE SoC). Extensive evaluation demonstrates that LTE2B achieves a robust (> 99% accuracy) and long distance (> 400 m) CTC performance under a full range of wireless configurations including indoor/outdoor, mobility, and duty-cycle settings. ")])])])
